* {
	box-sizing: border-box;
}

body {
	font-family: "Roboto", "Arial", sans-serif;
	font-size: 14px;
	line-height: 1.71;
	letter-spacing: 0.03em;
	margin: 0;
}

form {
	display: block;
}

ul,
ol {
	list-style: none;
	padding: 0;
	margin: 0;
}

a {
	text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
	margin: 0;
}

img,
svg {
	display: block;
	max-width: 100%;
	height: auto;
}
